.about_me_navigation__options {
  display: flex;
  justify-content: space-around;
  list-style: none;
  margin-top: 60px;
  z-index: 1;
}
.about_me_navigation__options ul {
  display: flex;
  justify-content: space-around;
  width: 100%;
  margin-left: 4%;
  border-bottom: 6px solid rgb(0, 207, 0);
  z-index: 1;
}

.about_me_navigation__option {
  color: rgb(135, 148, 151);
  text-decoration: none;
  text-decoration: 0;
  font-size: 38px;
  z-index: 1;
  padding-bottom: 45px;
}
.about_me_navigation__options .active {
  color: rgb(0, 247, 0);
}
.about_me_navigation__options .active .line {
  position: relative;
  opacity: 1;
  transform: translateY(-660%);
  color: rgb(0, 247, 0);
  border-radius: 5px;
}

.about_me_navigation__option .line {
  position: relative;
  width: 100%;
  height: 10%;
  border-bottom: 6px solid rgb(0, 247, 0);
  transition: all 3s ease-in-out;
  opacity: 0;
}

//rescponsivness
@media only screen and (max-width: 1023px) {
  .about_me_navigation__options {
    margin-top: 50px;
  }
  .about_me_navigation__option {
    font-size: 35px;
    padding-bottom: 25px;
  }
  .about_me_navigation__options ul {
    margin-left: 0%;
  }
  .about_me_navigation__option .line {
    height: 10%;
    border-bottom: 3px solid rgb(0, 247, 0);
  }
}
@media only screen and (max-width: 768px) {
  .about_me_navigation__options {
    margin-top: 50px;
  }
  .about_me_navigation__option {
    font-size: 25px;
    padding-bottom: 25px;
  }
  .about_me_navigation__options ul {
    width: 100%;
  }
  .about_me_navigation__option .line {
    height: 5%;
    border-bottom: 3px solid rgb(0, 247, 0);
  }
}

@media only screen and (max-width: 535px) {
  .about_me_navigation__option {
    font-size: 15px;
    padding-bottom: 20px;
  }
}
