.contact {
  color: white;
  height: 100vh;
  overflow: hidden;
  background-color: black;
  z-index: 1;
}
.contact__header {
  position: relative;
  font-family: Raleway;
  margin: auto;
  margin-top: 7vh;
  margin-bottom: 3vh;
  text-align: center;
  font-size: 32px;
  border-radius: 30px;
  width: 500px;
  height: 100px;
  z-index: 3;
}
.contact__header h1 {
  position: relative;
  top: 30%;
  z-index: 1;
  cursor: default;
  animation: blink 12s infinite;
}
.contact__header:hover h1 {
  text-shadow: 0 0 9px rgb(0, 247, 0), 0 0 19px rgb(0, 247, 0),
    0 0 24px rgb(0, 247, 0), 0 0 46px rgb(0, 247, 0), 0 0 66px rgb(0, 247, 0),
    0 0 14px rgb(0, 247, 0), 0 0 105px rgb(0, 247, 0);
}
.contact__body {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  height: 70%;
}
.contact__info_container {
  z-index: 1;
  text-align: center;
  font-size: 20px;
}
.contact__info_container h2 {
}
.contact__info_content_container {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  height: 55vh;
  width: 55vw;
  margin-top: 40px;
  border: 3px solid rgb(0, 247, 0);
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 30px;
  margin-left: 3vw;
}
.contact__info_content_container i {
  position: relative;
  left: -20px;
  font-size: 24px;
}
.contact__info_content_container span {
  font-size: 24px;
  letter-spacing: 1px;
}

.contact__email {
  margin-top: 10px;
  position: relative;
  font-size: 20px;
}
.contact__email:hover i,
.contact__phone:hover i {
  text-shadow: 0 0 9px rgb(0, 247, 0), 0 0 19px rgb(0, 247, 0),
    0 0 24px rgb(0, 247, 0), 0 0 46px rgb(0, 247, 0), 0 0 66px rgb(0, 247, 0),
    0 0 14px rgb(0, 247, 0), 0 0 105px rgb(0, 247, 0);
  transition: 0.2s ease-out;
}
.contact__phone {
  position: relative;
  font-size: 20px;
}
.contact__social_media {
  position: relative;
  left: 20px;
}
.contact__social_media i {
  margin: 0 45px 0 45px;
  transform: scale(1.2);
  transition: 0.2s ease-in-out;
  cursor: pointer;
  color: white;
}
.contact__social_media i:hover {
  color: rgb(0, 247, 0);
  transform: scale(1.7) rotate(360deg);
  transition: 0.1s ease-in-out;
}
.contact__email_btn {
  height: 90px;
  width: 200px;
  position: relative;
  margin-bottom: 10px;
  font-size: 24px;
  background-color: rgba(255, 255, 255, 0);
  padding: 12px 20px 12px 20px;
  color: white;
  border: 2px solid rgb(0, 247, 0);
  border-radius: 30px;
  outline: none;
  cursor: pointer;
}

// responsiveness

@media only screen and (max-width: 767px) {
  .contact {
    position: relative;
    left: 0px;
  }

  .contact__header {
    width: 300px;
    height: 90px;
  }
  .contact__header h1 {
    font-size: 42px;
  }
  .contact__info_content_container {
    height: 55vh;
    width: 55vw;
    margin-top: 40px;
    border: none;
    background-color: rgba(0, 0, 0, 0.5);
    border-radius: 30px;
    margin-left: 3vw;
  }
  .contact__info_container h2 {
    font-size: 24px;
  }
  .contact__info_content_container i {
    font-size: 20px;
  }
  .contact__info_content_container span {
    font-size: 20px;
  }
  .contact__info_content_container .line {
    width: 100%;
    height: 2px;
    position: absolute;
    background-color: rgb(0, 247, 0);
    z-index: 1;
    bottom: -50%;
  }
  .contact__email,
  .contact__phone {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  .contact__email i,
  .contact__phone i {
    left: 0;
    padding-bottom: 15px;
  }

  .contact__email_btn {
    height: 50px;
    width: 150px;
    font-size: 18px;
  }
}
@media only screen and (max-width: 625px) {
  .contact {
    left: 0px;
  }
  .contact__header {
    width: 300px;
    height: 80px;
  }
  .contact__header h1 {
    font-size: 38px;
  }
  .contact__info_content_container i {
    font-size: 16px;
  }
  .contact__info_content_container span {
    font-size: 16px;
  }
}
@media only screen and (max-width: 450px) {
  .contact__info_content_container {
    width: 70vw;
  }
}
