.skills {
  position: relative;
  z-index: 1;
  text-align: center;
  font-family: "Playfair Display";
  margin-top: 50px;
  margin-bottom: 50px;
  overflow: visible;
}
.skills__container {
  display: flex;
  position: relative;
  justify-content: space-around;
  text-align: center;
  color: white;
  width: 100%;
}
.skills h1 {
  margin-top: 6vh;
  margin-bottom: 3vh;
  z-index: 1;
  color: white;
  text-shadow: -1px -1px 1px rgb(3, 187, 3), -1px 1px 1px rgb(3, 187, 3),
    1px -1px 1px rgb(3, 187, 3), 1px 1px 1px rgb(3, 187, 3),
    0 0 5px rgb(3, 187, 3), 0 0 15px rgb(3, 187, 3), 0 0 25px rgb(3, 187, 3),
    0 0 40px rgb(3, 187, 3), 0 0 50px rgb(3, 187, 3), 0 0 60px rgb(3, 187, 3),
    0 0 80px rgb(3, 187, 3), 0 0 110px rgb(3, 187, 3), 0 0 210px rgb(3, 187, 3);
}
.skills__images_container {
  position: relative;
  background-color: rgba(0, 0, 0, 0.5);
  width: 40%;
  height: 100%;
  margin-top: 6vh;
  margin-left: 10vw;
  border-radius: 10px;
}

.skills h2 {
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  padding: 32px;
  color: rgb(3, 187, 3);
  font-size: 28px;
  width: 100%;
}

.skills__text_container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 40%;
  margin-top: 6vh;
  height: auto;
  text-align: left;
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 10px;
  margin-right: 10vw;
}

.skills__text_container span {
  font-size: 30px;
  padding: 2vh;
  width: 80%;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
}

.skills__images {
  z-index: 1;
  max-width: 100%;
  display: flex;
  height: 50%;
}
.skills__images ul {
  display: flex;
  list-style: none;
  justify-content: center;
  width: 100%;
  z-index: 1;
  flex-wrap: wrap;
}

.skills__images img {
  max-width: 10vh;
  max-height: 10vh;
  z-index: 1;
  margin: 25px;
}

//Responsivness
@media only screen and (max-width: 2000px) {
  .skills__text_container span {
    font-size: 30px;
    line-height: 28px;
  }
}

@media only screen and (max-width: 2000px) {
  .skills__images_container {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
  }

  .skills h2 {
    margin: 0;
  }
  .skills__text_container {
    display: flex;
    flex-direction: column;
    margin-bottom: 2vh;
  }
  .skills__text_container span {
    font-size: 26px;
  }
  .skills__images img {
    max-width: 80px;
    max-height: 80px;
  }
}
@media only screen and (max-width: 1700px) {
  .skills__container {
    justify-content: space-around;
  }
  .skills__text_container span {
    position: relative;
    margin-bottom: 2vh;
  }
}
@media only screen and (max-width: 1450px) {
  .skills__images ul {
    margin: auto;
    width: 50vh;
    flex-wrap: wrap;
  }
  .skills__text_container span {
    font-size: 18px;
  }
}
@media only screen and (max-width: 1200px) {
  .skills__text_container span {
    font-size: 16px;
  }
}
@media only screen and (max-width: 1023px) {
  .skills {
    left: 5%;
  }
  .skills h1 {
    position: relative;
    margin-bottom: 40px;
  }

  .skills__container {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-bottom: 50px;
  }
  .skills__images_container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    margin: 0;
  }

  .skills__text_container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    position: relative;
    width: 70%;
  }
  .skills__text_container span {
    font-size: 20px;
  }
}
@media only screen and (max-width: 768px) {
  .skills {
    left: 0;
  }
  .skills__text_container {
    width: 90%;
  }
}

@media only screen and (max-width: 700px) {
  .skills__text_container {
    left: 0;
    margin: 30px 0;
  }
  .skills__images img {
    max-width: 70px;
    max-height: 70px;
  }
  .skills__text_container span {
    font-size: 20px;
  }
}
@media only screen and (max-width: 460px) {
  .skills__text_container {
    width: 90%;
  }
  .skills__images_container {
    justify-content: space-around;
    width: 65%;
  }
  .skills__images img {
    max-width: 55px;
    max-height: 55px;
    margin: 10px;
  }
  .skills__text_container span {
    font-size: 12px;
  }
}
